(function() {
  'use strict';

  angular
    .module('testGrowth', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
      .module('testGrowth')
      .factory('carsApi', carsApi);

  /** @ngInject */
  function carsApi($q, $resource) {
    var defered = null;
    var apiUrl = './data/cars.json';
    var service = {
      apiUrl: apiUrl,
      getCars: getCars
    };
    var carsMethods = {
      get:{isArray: true}
    };
    return service;

    function getCars() {
      defered = $q.defer();
      var Data = $resource(apiUrl,{},carsMethods);
      Data.get({isArray: false},
        function(response) {
          var cars = [];
          angular.forEach(response, function(e){ cars.push(e); });
          defered.resolve(cars);
        },
        function(response){
          return defered.reject(response);
        }
      );
      return defered.promise;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';

    activate();

    function activate() {
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .controller('CompareController', CompareController);

  /** @ngInject */
  function CompareController($timeout, carsApi) {
    var vm = this;

    vm.cars = [];
    vm.car1 = null;
    vm.car2 = null;

    activate();

    function activate() {
      getCarsApi();
    }

    function getCarsApi() {
        carsApi.getCars().then(function(cars){
          vm.cars = cars;
        });
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .controller('CarsController', CarsController);

  /** @ngInject */
  function CarsController($timeout, carsApi, $state) {
    var vm = this;

    vm.cars = [];
    vm.showCarDetails = showCarDetails;

    activate();

    function activate() {
      getCarsApi();
    }

    function getCarsApi(){
        carsApi.getCars().then(function(cars){
          vm.cars = cars;
        });
    }

    function showCarDetails(car) {
      $state.go('details',{carId:encodeURIComponent(car.model),car:car});
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .controller('CarController', CarController);

  /** @ngInject */
  function CarController($timeout, carsApi, $stateParams, $state){
    var vm = this;
    if(angular.isObject($stateParams.car)){
      vm.car = $stateParams.car;
    }else{
      $state.go('cars');
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      }).state('compare', {
        url: '/compare',
        templateUrl: 'app/compare/compare.html',
        controller: 'CompareController',
        controllerAs: 'compare'
      }).state('cars', {
        url: '/cars',
        templateUrl: 'app/cars/cars.html',
        controller: 'CarsController',
        controllerAs: 'cars'
      }).state('details', {
        url: '/cars/:carId',
        params: { car: null },
        templateUrl: 'app/car/car.html',
        controller: 'CarController',
        controllerAs: 'car'
      });
    $locationProvider.html5Mode(true);
    $urlRouterProvider.otherwise('/');
  }

})();

/* global moment:false */
(function() {
  'use strict';

  angular
    .module('testGrowth')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('testGrowth')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

